import { LocalStorageService } from './../../../shared/local-storage.service';
import { EdaService } from './../eda.service';
import { EdaComponent } from './../eda.component';
import { AfterViewInit, Component, Inject, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { ToastrService } from 'ngx-toastr';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatSnackBar, MatSnackBarRef } from '@angular/material/snack-bar';
import { GridOptions } from 'ag-grid-community';
import { SpinnerService } from 'src/app/shared/spinner.service';
import { DataService } from '../../data/data.service';
import { UtilsService } from 'src/app/shared/utils.service';

const Plotly = require('plotly.js-dist');

@Component({
  selector: 'app-dialog-handler',
  templateUrl: './dialog-handler.component.html',
  styleUrls: ['./dialog-handler.component.scss'],
})
export class DialogHandlerComponent implements OnInit, OnDestroy, AfterViewInit {
  /** Data table settings */
  dataSource: MatTableDataSource<[]> = new MatTableDataSource();
  // @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  snackBarRef: MatSnackBarRef<any>;
  /** Graph types for multi/uni variate */
  graphTypes: any = {
    Integer: ['Barplot', 'Boxplot', 'Scatterplot', 'Lineplot'],
    Decimal: ['Barplot', 'Boxplot', 'Scatterplot', 'Lineplot'],
    Categorical: ['Barplot', 'Pieplot'],
  };
  gridOptions: GridOptions;
  resizeEvent = 'resize.ag-grid';
  private gridApi;
  gridColumnApi: any;
  previewrowData: any;
  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  /** From control instance variables */
  multiUniFeaturesControl = new FormControl('');

  // pageSizeOptions: Array<number> = [10, 15, 20];
  displayedColumns: Array<string> = [];
  de: any;
  backGround: string = '#ffffff';
  foreGround: string = '#08509d';
  featureDataTypes: Array<any> = [];
  allFeatures: Array<any> = [];
  ftype: any;
  graphTypesSelected: Array<any> = [];
  graphType: string;
  mySelections: any;
  sencondftype: any;
  thirdftype: any;
  project: any;
  showSpinner: boolean = false;
  isMmultiUnivariateAnalysis: boolean = true;
  isWordCloud: boolean = true;
  dialogHeading: string;
  feature: any;
  outlier: any = false;
  showOutlier: boolean = false;
  movingAverage: any = false;
  method: any;
  span: any;
  baseImg: any;
  movingAverageMethod = [
    {
      name: 'Exponential moving average',
      method: 'exponential_ma'
    },
    {
      name: 'Simple moving average',
      method: 'simple_ma'
    }
  ]
  showhint: any;
  constructor(
    @Inject(MAT_DIALOG_DATA) public dialogData: any,
    private _fb: FormBuilder,
    private _edaDialogRef: MatDialogRef<EdaComponent>,
    private _eda: EdaService,
    private _localStorage: LocalStorageService,
    private _toaster: ToastrService,
    private _snackBar: MatSnackBar,
    private _spinner: SpinnerService,
    private _data: DataService,
    public _utils: UtilsService
  ) { }

  /**
   * Initializes all the required data
   */
  ngOnInit(): void {
    this.feature = [];
    switch (this.dialogData.type) {
      case 'preview':
        this.dialogHeading = 'Data Preview';
        this.previewData();
        break;
      case 'analysis':
        this.dialogHeading = 'Multivariate/Univariate Analysis';
        this.project = this._localStorage.getItem('project');
        this.dialogData.featureDataTypes.forEach((data: any) => {
          data.data !== 'Unstructured' &&
            this.allFeatures.push({ feature: data.colName, isDisabled: false });
        });

        this.feature = this.allFeatures;
    }
  }

  /**
   * Assigns the references after the view is initialized
   */
  ngAfterViewInit() {
    // this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  /**
   * Finds the data type
   * @param targetVar
   * @returns
   */
  findTheDataType(targetVar: any): any {
    return this.dialogData.featureDataTypes.find((e: any) => {
      return e.colName === targetVar;
    });
  }

  /**
   * Filters the array
   * @param myArray
   * @param itemToRemove
   * @returns
   */
  filterArray(myArray: any, itemToRemove: any) {
    return myArray.filter((arrayItem: any) => {
      return arrayItem !== itemToRemove;
    });
  }

  previewData() {
    this._spinner.showSpinner();
    let columnDefs = [];
    this._data
      .myFileData(this.dialogData.reqObj.pid, this.dialogData.dataId, this.dialogData.reqObj, true)
      .subscribe(
        (res: any) => {
          if (res) {
            for (let key in res.preview[0]) {
              columnDefs.push({
                headerName: key,
                field: key,
              });
            }

            this.gridOptions = {
              headerHeight: 40,
              columnDefs: columnDefs,
              // rowData: res.preview,
              defaultColDef: { resizable: true },
            };

            this.previewrowData = res.preview;
            this._spinner.stopSpinner();
          } else {
            this._spinner.stopSpinner();
            this._toaster.error('Cannot preview the files data', 'Data Set');
          }
        },
        (err: Error) => {
          this.showSpinner = false;
          this._toaster.error('Server Error', 'Data Set');
        }
      );
  }

  graphSelectionChanged() {
    if (this.graphType !== 'Lineplot') {
      this.movingAverage = false;
      this.outlier = false;
      this.method = '';
      this.span = '';
    }
    this.showOutlier = (this.project.type === 'timeseries' && this.graphType === 'Lineplot' && this.dialogData.edaData.hasOwnProperty('edaSummary')) ? true : false;
  }
  /**
   * Feature selection changed for multi/uni variate
   */
  featureSelectionChanged(): void {
    this.showOutlier = (this.project.type === 'timeseries' && this.multiUniFeaturesControl.value.length === 1 && this.graphType === 'Lineplot' && this.dialogData.edaData.hasOwnProperty('edaSummary')) ? true : false;
    this.de = this.findTheDataType(this.multiUniFeaturesControl.value[0]);
    // if (
    //   (this.multiUniFeaturesControl.value.length != 0 && this.de.data == 'Text') ||
    //   (this.multiUniFeaturesControl.value.length != 0 && this.de.data == 'URL')
    // ) {
    //   this.allFeatures.forEach((data: any) => {
    //     if (data.feature !== this.multiUniFeaturesControl.value[0]) {
    //       data.disabled = true;
    //     }
    //   });
    // } else if (this.multiUniFeaturesControl.value.length === 0) {
    //   this.allFeatures.forEach((data: any) => {
    //     data.disabled = false;
    //   });
    // } else {
    this.allFeatures = [];
    // }
    let dt: Array<any> = [];
    // Hides the plot section if no feature is selected
    if (this.multiUniFeaturesControl.value.length === 0) {
      this.isWordCloud = false;
      this.isMmultiUnivariateAnalysis = false;
    }
    // limits the user for selecting at most 3 options
    if (this.multiUniFeaturesControl.value.length < 4) {
      this.mySelections = this.multiUniFeaturesControl.value;
    } else {
      this.multiUniFeaturesControl.setValue(this.mySelections);
      this.multiUniFeaturesControl.updateValueAndValidity();
    }
    // Generates radio buttons as per the selected features
    if (this.multiUniFeaturesControl.value.length === 1) {
      this.ftype = this.findTheDataType(this.multiUniFeaturesControl.value[0]);
      this.graphTypesSelected = this.graphTypes[this.ftype.multivariateDataType];
      switch (this.ftype.multivariateDataType) {
        case 'Integer':
          dt = ['Integer', 'Decimal', 'Datetime', 'Categorical', 'Boolean'];
        case 'Decimal':
          dt = ['Integer', 'Decimal', 'Datetime', 'Categorical', 'Boolean'];
          break;
        case 'Categorical':
        case 'Boolean':
          dt = ['Integer', 'Decimal', 'Categorical', 'Boolean'];
          break;
        case 'Datetime':
          dt = ['Integer', 'Decimal'];
          break;
        case 'Text':
          dt = [];
          break;
        case 'URL':
          dt = [];
          break;
        default:
          dt = [];
      }
      let temp = this.graphTypes[this.ftype.multivariateDataType];
      if (temp) {
        this.graphTypesSelected = this.filterArray(temp, 'Scatterplot');
      }
      this.graphType = 'Barplot';
      // Remove barplot and add histogram, if only one feature is selected and is numeric
      if (
        this.multiUniFeaturesControl.value.length === 1 &&
        (this.ftype.multivariateDataType === 'Integer' ||
          this.ftype.multivariateDataType === 'Decimal')
      ) {
        this.graphTypesSelected = this.graphTypesSelected.filter((e) => e != 'Barplot');
        this.graphTypesSelected.push('Histogram');
        this.graphType = 'Histogram';
      }
    } else if (this.multiUniFeaturesControl.value.length === 2) {
      this.graphTypesSelected = [];
      this.ftype = this.findTheDataType(this.multiUniFeaturesControl.value[0]);
      if (
        this.ftype.multivariateDataType === 'Integer' ||
        this.ftype.multivariateDataType === 'Decimal' ||
        this.ftype.multivariateDataType === 'Boolean' ||
        this.ftype.multivariateDataType === 'Categorical'
      ) {
        this.sencondftype = this.findTheDataType(this.multiUniFeaturesControl.value[1]);
        if (
          this.ftype.multivariateDataType === 'Integer' &&
          this.sencondftype.multivariateDataType === 'Integer'
        ) {
          this.graphTypesSelected = ['Boxplot', 'Scatterplot'];
          this.graphType = 'Boxplot';
        }

        if (
          this.ftype.multivariateDataType === 'Decimal' &&
          this.sencondftype.multivariateDataType === 'Decimal'
        ) {
          this.graphTypesSelected = ['Boxplot', 'Scatterplot'];
          this.graphType = 'Boxplot';
        }

        if (
          this.ftype.multivariateDataType === 'Integer' &&
          this.sencondftype.multivariateDataType === 'Decimal'
        ) {
          this.graphTypesSelected = ['Boxplot', 'Scatterplot'];
          this.graphType = 'Boxplot';
        }

        if (
          this.ftype.multivariateDataType === 'Decimal' &&
          this.sencondftype.multivariateDataType === 'Integer'
        ) {
          this.graphTypesSelected = ['Boxplot', 'Scatterplot'];
          this.graphType = 'Boxplot';
        }

        if (
          this.ftype.multivariateDataType === 'Integer' &&
          this.sencondftype.multivariateDataType === 'Numeric'
        ) {
          this.graphTypesSelected = ['Boxplot', 'Scatterplot'];
          this.graphType = 'Boxplot';
        }

        if (
          this.ftype.multivariateDataType === 'Numeric' &&
          this.sencondftype.multivariateDataType === 'Integer'
        ) {
          this.graphTypesSelected = ['Boxplot', 'Scatterplot'];
          this.graphType = 'Boxplot';
        }

        if (
          this.ftype.multivariateDataType === 'Numeric' &&
          this.sencondftype.multivariateDataType === 'Numeric'
        ) {
          this.graphTypesSelected = ['Boxplot', 'Scatterplot'];
          this.graphType = 'Boxplot';
        }
        if (
          this.ftype.multivariateDataType === 'Integer' &&
          this.sencondftype.multivariateDataType === 'Boolean'
        ) {
          this.graphTypesSelected = ['Barplot', 'Boxplot', 'Histogram'];
          this.graphType = 'Barplot';
        } else if (
          this.ftype.multivariateDataType === 'Boolean' &&
          this.sencondftype.multivariateDataType === 'Integer'
        ) {
          this.graphTypesSelected = ['Barplot', 'Boxplot', 'Histogram'];
          this.graphType = 'Barplot';
        } else if (
          this.ftype.multivariateDataType === 'Boolean' &&
          this.sencondftype.multivariateDataType === 'Decimal'
        ) {
          this.graphTypesSelected = ['Barplot', 'Boxplot', 'Histogram'];
          this.graphType = 'Barplot';
        } else if (
          this.ftype.multivariateDataType === 'Decimal' &&
          this.sencondftype.multivariateDataType === 'Boolean'
        ) {
          this.graphTypesSelected = ['Barplot', 'Boxplot', 'Histogram'];
          this.graphType = 'Barplot';
        } else if (
          this.ftype.multivariateDataType === 'Boolean' &&
          this.sencondftype.multivariateDataType == 'Numeric'
        ) {
          this.graphTypesSelected = ['Barplot', 'Boxplot', 'Histogram'];
          this.graphType = 'Barplot';
        } else if (
          this.ftype.multivariateDataType === 'Numeric' &&
          this.sencondftype.multivariateDataType === 'Boolean'
        ) {

          this.graphTypesSelected = ['Barplot', 'Boxplot', 'Histogram'];
          this.graphType = 'Barplot';
        } else if (
          this.ftype.multivariateDataType === 'Categorical' &&
          this.sencondftype.multivariateDataType === 'Integer'
        ) {
          this.graphTypesSelected = ['Barplot', 'Boxplot', 'Histogram'];
          this.graphType = 'Barplot';
        } else if (
          this.ftype.multivariateDataType === 'Integer' &&
          this.sencondftype.multivariateDataType === 'Categorical'
        ) {
          this.graphTypesSelected = ['Barplot', 'Boxplot', 'Histogram'];
          this.graphType = 'Barplot';
        } else if (
          this.ftype.multivariateDataType === 'Categorical' &&
          this.sencondftype.multivariateDataType === 'Decimal'
        ) {
          this.graphTypesSelected = ['Barplot', 'Boxplot', 'Histogram'];
          this.graphType = 'Barplot';
        } else if (
          this.ftype.multivariateDataType === 'Decimal' &&
          this.sencondftype.multivariateDataType === 'Categorical'
        ) {
          this.graphTypesSelected = ['Barplot', 'Boxplot', 'Histogram'];
          this.graphType = 'Barplot';
        } else if (
          this.ftype.multivariateDataType === 'Categorical' &&
          this.sencondftype.multivariateDataType === 'Numeric'
        ) {
          this.graphTypesSelected = ['Barplot', 'Boxplot', 'Histogram'];
          this.graphType = 'Barplot';
        } else if (
          this.ftype.multivariateDataType === 'Numeric' &&
          this.sencondftype.multivariateDataType === 'Categorical'
        ) {
          this.graphTypesSelected = ['Barplot', 'Boxplot', 'Histogram'];
          this.graphType = 'Barplot';
        }

        dt = ['Integer', 'Decimal', 'Categorical', 'Boolean'];
      } else if (this.ftype.multivariateDataType === 'Datetime') {
        dt = [];
      } else if (this.ftype.multivariateDataType === 'Categorical') {
        dt = ['Integer', 'Decimal', 'Categorical', 'Boolean'];
      } else if (this.ftype.multivariateDataType === 'Boolean') {
        dt = ['Integer', 'Decimal', 'Categorical', 'Boolean'];
      } else if (this.ftype.multivariateDataType === 'Text') {
        dt = [];
      } else if (this.ftype.multivariateDataType === 'URL') {
        dt = [];
      }
    } else if (this.multiUniFeaturesControl.value.length === 3) {
      this.thirdftype = this.findTheDataType(this.multiUniFeaturesControl.value[2]);
      this.ftype.isNumeric = (this.ftype.multivariateDataType === 'Integer' || this.ftype.multivariateDataType === 'Decimal'
        || this.ftype.multivariateDataType === 'Numeric') ? 'Numeric' : 'none';
      this.sencondftype.isNumeric = (this.sencondftype.multivariateDataType === 'Integer' ||
        this.sencondftype.multivariateDataType === 'Decimal' ||
        this.sencondftype.multivariateDataType === 'Numeric') ? 'Numeric' : 'none';
      this.thirdftype.isNumeric = (this.thirdftype.multivariateDataType === 'Integer'
        || this.thirdftype.multivariateDataType === 'Decimal'
        || this.thirdftype.multivariateDataType === 'Numeric') ? 'Numeric' : 'none';
      if (this.ftype.isNumeric === 'Numeric' && this.sencondftype.isNumeric === 'Numeric' && this.thirdftype.isNumeric === 'Numeric') {
       //   console.log('m inside**********************************')
        this.graphTypesSelected = ['2D', '3D'];
        this.graphType = '2D';
      } else {
       // console.log("**********************************pls let else")
        this.graphType = 'scatter';
      }
    }
    this.dialogData.featureDataTypes.forEach((data: any) => {
      dt.indexOf(data.multivariateDataType) !== -1
        ? this.allFeatures.push({ feature: data.colName, isDisabled: false })
        : null;
    });

    if (this.multiUniFeaturesControl.value.length) {
      let featureValue;
      if (this.multiUniFeaturesControl.value.length == 1) {
        featureValue = this.multiUniFeaturesControl.value[0];
      } else if (this.multiUniFeaturesControl.value.length == 2) {
        featureValue = this.multiUniFeaturesControl.value[1];
      }

      let item = this.dialogData.featureDataTypes.filter((item) => {
        return item.data == 'Datetime' && item.colName == featureValue;
      });

      if (item.length && item[0].data == 'Datetime') {
        item.length == 1
          ? this.allFeatures.push({ feature: item[0].colName, isDisabled: false })
          : null;
        // this.allFeatures.unshift(this.allFeatures.pop())
      }
    }
    if (this.allFeatures.length == 0) {
      this.multiUniFeaturesControl.value.forEach((element) => {
        this.allFeatures.push({ feature: element });
      });
    }

    if (this.multiUniFeaturesControl.value.length == 0) {
      this.allFeatures = this.feature;
    }
  }

  compareFn(optionOne, optionTwo): boolean {
    return optionOne.id === optionTwo.id;
  }
  /**
   * Generates a graph as per the selected features and plot type
   */
  onSubmitFeatureAnalysis(): void {
    this.baseImg = undefined;
    this.showSpinner = true;
    if (this.movingAverage === true && (this.method === undefined || this.span === undefined)) {
      this.snackBarRef = this._snackBar.open(
        'Please select method and enter span value when moving average plot is selected',
        'OK'
      );
      return;
    }
    if (this.movingAverage === true && this.method !== undefined && (this.span < 0 || this.span === 0)) {
      this.snackBarRef = this._snackBar.open(
        'Span value can not be less than 1',
        'OK'
      );
      return;
    }
    if (this.movingAverage === true && (this.span % 1) !== 0) {
      this.snackBarRef = this._snackBar.open(
        'Span value can not be decimal',
        'OK'
      );
      return;
    }
    this.isWordCloud = true;
    this.isMmultiUnivariateAnalysis = true;
    let chartType;
    // If histogram is selected then parse graph type as Barplot, only if one feature is selected and is numeric
    if (
      this.multiUniFeaturesControl.value.length === 1 &&
      this.graphType == 'Histogram' &&
      (this.ftype.multivariateDataType === 'Integer' ||
        this.ftype.multivariateDataType === 'Decimal')
    ) {
      chartType = 'Barplot';
    } else {
      chartType = this.graphType;
    }
    let reqObj: any = {
      backGround: this.backGround,
      foreGround: this.foreGround,
      selectedVariables: this.multiUniFeaturesControl.value,
      chartType: chartType,
      outlier: this.outlier,
      movingAverage: this.movingAverage,
      method: this.method,
      span: this.span
    };
    if (this.dialogData.edaData.hasOwnProperty('edaSummary')) {
      reqObj.edaSummary = this.uniqueBy(this.dialogData.edaData.edaSummary, 'colName');
      reqObj.filename = this.dialogData.selectedDataSet.name;
      reqObj.currentEdaId = this.dialogData.edaData._id;
      reqObj.currentPipelineFilename = this.dialogData.edaData.tasks[0].currentPipelineFilename;
      reqObj.featureDataTypes = this.uniqueBy(this.dialogData.featureDataTypes, 'colName');
    } else {
      reqObj.featureDataTypes = this.uniqueBy(this.dialogData.featureDataTypes, 'colName');
      reqObj.filename = this.dialogData.selectedDataSet.name;
    }
    reqObj.fileEncoding = this.dialogData.selectedDataSet.fileEncoding || 'utf_8';
    this.generatePlot(reqObj);
  }

  /**
   * Generate and renders the plot for munti uni variate feature
   * @param reqObj
   */
  generatePlot(reqObj: any): void {
    this._spinner.showSpinner();
    this._eda
      .generateMultiUniVariatePlot(this.project._id, reqObj)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          this.showSpinner = false;
          if (res) {
            this.showhint = res.sampling_msg;
            let wordCloud: any = document.getElementById('wordCloud');
            let graphDiv: any = document.getElementById('multiUnivariateAnalysis');
            if (
              this.ftype.multivariateDataType === 'Text' ||
              this.ftype.multivariateDataType === 'URL'
            ) {
              this.baseImg = res.graphContent;
              this.isWordCloud = true;
              this.isMmultiUnivariateAnalysis = false;
              wordCloud.setAttribute('src', 'data:application/pdf;base64,' + res.graphContent);
            } else {
              this.isWordCloud = false;
              this.isMmultiUnivariateAnalysis = true;
              // Always clears the previous plot and renders new one.
              while (graphDiv.data && graphDiv.data.length > 0) {
                Plotly.deleteTraces(graphDiv, [0]);
              }
              res.graphContent.layout.template.layout.plot_bgcolor = 'white';
              res.graphContent.layout.width = 1138;
              res.graphContent.layout.height = 450;
              res.graphContent.layout.template.layout.title.xanchor = 'center';
              res.graphContent.layout.template.layout.title.y = 0.9;
              res.graphContent.layout.template.layout.title.x = 0.5;
              res.graphContent.layout.template.layout.title.yanchor = 'top';
              res.graphContent.layout.template.layout.xaxis.linecolor = 'black';
              res.graphContent.layout.template.layout.yaxis.linecolor = 'black';
              res.graphContent.layout.template.layout.xaxis.zerolinecolor = 'black';
              res.graphContent.layout.template.layout.yaxis.zerolinecolor = 'black';

              Plotly.plot(graphDiv, res.graphContent.data, res.graphContent.layout, { displayModeBar: true });

            }
          } else {
            this._toaster.error('Cannot plot the graph', 'EDA');
          }
          this._spinner.stopSpinner();
        },
        (err: any) => {
          this.showSnackBar(err.error.graphContent, 'OK');
          this._spinner.stopSpinner();
        }
      );
  }

  ondownload() {
    this._spinner.showSpinner();
    var a = document.createElement("a"); //Create <a>
    a.href = "data:image/png;base64," + this.baseImg; //Image Base64 Goes here
    a.download = "FeatureAnalaysis.png"; //File name Here
    a.click(); //Downloaded file
    this._spinner.stopSpinner();
  }
  /**
   * Finds the unique item and returns it
   * @param arr
   * @param prop
   * @returns
   */
  uniqueBy(arr: any, prop: any): any {
    return arr.reduce((a: any, d: any) => {
      if (!a.includes(d[prop])) {
        a.push(d);
      }
      return a;
    }, []);
  }

  /**
   * Closes the current opened dialog
   */
  closeModal(data?: any): void {
    this._edaDialogRef.close(data);
  }

  /**
   * Renders the snackbar
   */
  showSnackBar(message: string, button: string): void {
    this.snackBarRef = this._snackBar.open(message, button, {
      horizontalPosition: 'right',
      verticalPosition: 'bottom',
    });
  }

  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  /**
   * Called when ng-grid is rendered in dom
   * @param event
   */
  gridReady(event: any) {
    event.api.setDomLayout('autoHeight');
    this.gridApi = event.api;
    this.gridApi.setColumnDefs(this.gridOptions.columnDefs);
    this.gridApi.setRowData(this.previewrowData);
    this.gridApi.refreshCells({ force: true });
  }
  /**
   * Unsubscribes the subscriptions after destroy
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
