import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root',
})
export class TrainingService {
  serverBaseUrl: string = environment.serverBaseUrl;

  constructor(private _http: HttpClient) { }

  /**
   * Fetches the previous training details
   * @param projectId
   * @param isEdaRequired
   * @returns
   */
  getPreviousTrainingDetails(projectId: string, isEdaRequired: boolean): Observable<any> {
    return this._http.get(
      `${this.serverBaseUrl}/api/v2/projects/${projectId}/trainmodel?isEdaRequired=${isEdaRequired}`
    );
  }

  /**
   * Generates the target plot data
   * @param projectId
   * @param reqObj
   * @returns
   */
  plotTargetGraph(projectId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/projects/${projectId}/report/trainmodel/multiunivariate?target=true`,
      reqObj
    );
  }

  /**
   * Function will return details like algouseGPU:false,Type,isMultilabel,testPercentage,isImbalanced etc.
   * On target variable selection.
   * @param projectId
   * @param reqObj
   * @returns
   */
  getTargetDetails(projectId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/projects/${projectId}/predictive/target_details`,
      reqObj
    );
  }

  /**
   * Calculates the feature score
   * @param projectId
   * @param reqObj
   * @returns
   */
  calculateFeatureScore(projectId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/projects/${projectId}/predictive/calculate_feature_score`,
      reqObj
    );
  }

  /**
   * calculates the sampling percentage
   * @param projectId
   * @param reqObj
   * @returns
   */
  calculateSamplingPercentage(projectId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/projects/${projectId}/training/sampling_percentage`,
      reqObj
    );
  }

  /**
   * Fetches the optimal clusters
   * @param projectId
   * @param reqObj
   * @returns
   */
  findOptimalClusters(projectId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/projects/${projectId}/clustering/visualize/optimise_clusters`,
      reqObj
    );
  }

  /**
   * Fetches the unique categories
   * @param projectId
   * @param reqObj
   * @returns
   */
  getUniqueCategories(reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/training/feature/unique_categories`,
      reqObj
    );
  }

  /**
   * Executes the predictive modeling type training
   * @param projectId
   * @param reqObj
   * @returns
   */
  startPredictiveTraining(projectId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/projects/${projectId}/trainmodel`,
      reqObj,
      { responseType: 'text' }
    );
  }

  /**
   * Executes the clustering type training
   * @param projectId
   * @param reqObj
   * @returns
   */
  startClusteringTraining(projectId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/projects/${projectId}/clustering/trainmodel`,
      reqObj,
      { responseType: 'text' }
    );
  }

  /**
   * Executes the timeseries project type training
   * @param projectId
   * @param reqObj
   * @returns
   */
  startTimeseriesTraining(projectId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/projects/${projectId}/timeseries/trainmodel`,
      reqObj,
      { responseType: 'text' }
    );
  }

  /**
   * Generates the visualize component graph
   * @param projectId
   * @param reqObj
   * @returns
   */
  getVisualizeComponentPlot(projectId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/projects/${projectId}/timeseries/visualize/component`,
      reqObj
    );
  }

  /**
   * Performs the stationary test
   * @param projectId
   * @param reqObj
   * @returns
   */
  startStationarityTest(projectId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/projects/${projectId}/timeseries/stationarity_test`,
      reqObj
    );
  }

  /**
   * Generates the auto correalation graph
   * @param projectId
   * @param reqObj
   * @returns
   */
  getAutoCorrelationGraph(projectId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/projects/${projectId}/timeseries/visualize/autocorrelation`,
      reqObj
    );
  }

  /**
   * Deletes the training
   * @param projectId
   * @param dataId
   */
  deleteTraining(trainingId: string, projectId: string, isLastTraining: boolean): Observable<any> {
    return this._http.delete(
      `${this.serverBaseUrl}/api/v2/training/${trainingId}?lastTraining=${isLastTraining}&projectId=${projectId}`
    );
  }

  /**
   * Starts the retraining execution
   * @param trainingId
   * @param modelId
   * @param reqObj
   * @returns
   */
  startReTraining(trainingId: string, modelId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/training/${trainingId}/models/${modelId}/retrain`,
      reqObj
    );
  }

  /**
   * Starts the model tuning execution
   * @param trainingId
   * @param modelId
   * @param reqObj
   * @returns
   */
  startModelTuning(trainingId: string, modelId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/training/${trainingId}/model/${modelId}/tune`,
      reqObj
    );
  }

  /**
   * Starts the execution of adv algorithm
   * @param trainingId
   * @param modelId
   * @param reqObj
   * @returns
   */
  startAdvTraining(trainingId: string, modelId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/v2/advtrainmodel/${trainingId}/models/${modelId}`,
      reqObj
    );
  }
}
